import React from 'react'
import NavLink from './header/nav-link'

var Footer = React.createClass({
  render() {
    var className = 'footer' + (this.props.isMobile ? ' mobile-footer' : '');

    return (
      <div className={className}>
        <div>
          <NavLink to="/about" onlyActiveOnIndex></NavLink>
          |
          <NavLink to="/about" onlyActiveOnIndex></NavLink>
          <NavLink to="/">: 400 0933 876</NavLink>
        </div>
        <div className='beian'>
          <a target="_blank" href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=50023402000162" >
            <img src="/assets/images/beian.png"/>
            <div> 50023402000162</div>
          </a>
          <div>ICPICP15009385-1</div>
        </div>
        <div className='beian'>
          <div>SP5002341610076106</div>
        </div>
        <div>
          2016  
        </div>
      </div>
    );
  }
});

export default Footer


