import React from 'react'
import Reflux from 'reflux'
import ReactCSSTransitionGroup from 'react/lib/ReactCSSTransitionGroup'

import Item from './components/products/item'
import Header from './components/header/header'
import Footer from '../../footer'
import mobileUtils from '../../utils/mobile-utils'

import store from './stores/category-products-store'
import actions from './actions/product-actions'

var CategoryProduct = React.createClass({
  mixins: [Reflux.connect(store), Reflux.ListenerMixin],

  componentDidMount() {
    actions.loadProductsByCategory(this.props.params.category);
  },

  render() {
    var items = this.state.products.map((item, i) => {
      item.type = this.props.params.category;
      return <Item key={item.id} item={item} id={item.id} index={i} />;
    });

    var style = {height: this.props.height};
    var titleMapping = {
      self_sale: '',
      kaixian_special: '',
      imported: '',
      daily_usage: '',
      beautify: '',
      camera: '',
      home_machine: '',
      heritage: '',
      green: '',
      organic: '',
      foundation: '',
      sea_food: "",
      bacon: "",
      navel_orange: "",
      pomelo: "",
      seasonal_fresh: "",
      gin: "",
      wine: "",
      grape_wine: "",
      wine_drink: "",
      dendrobii: "",
      wolfberry: "",
      honey: "",
      nourishing_health: "",
      drink: "",
      herbal_tea: "",
      green_tea: "",
      red_tea: "",
      drink_tea: "",
      seafood: "",
      fried: "",
      candied_fruit: "",
      snacks: "",
      flavor: "",
      dry_goods: "",
      oil: "",
      rice: "",
      grain_oil: "",
      dade: "",
      zhuxi: "",
      zishui: "",
      manyue: "",
      vilage: "",
      xiangcuntuhuo: "",
      gongyichanpin: "",
      gongyechanping: "",
      xianwaichanping: ""
    };

    var containerClass = 'products-container' + (mobileUtils.mobileCheck() ? ' mobile-products-container' : '');

    return (
      <div className={containerClass} style={style}>
        <div className='product-sub-page'>
          <div className='product-list-container'>
            <h1>{titleMapping[this.props.params.category]}</h1>
        {this.props.children}
            <ReactCSSTransitionGroup component="ul" className="pure-g appItems list-reset" id="item-group"  transitionName="itemTransition" transitionLeave={false} transitionEnterTimeout={200} transitionLeaveTimeout={200}>
          {items}
            </ReactCSSTransitionGroup>
          </div>
        </div>
      </div>
    );
  }
});

export default CategoryProduct




