import React from 'react'
import Basket from '../basket/basket'
import ShoppingCart from '../basket/shopping-cart'
import NavLink from '../../../../header/nav-link'
import ProductCategory from './product-category'
import MenuItem from './menu-item'
import auth from '../../../../utils/auth'
import SearchBar from '../../../../header/search-bar'

var Header =
  React.createClass({
    displayBasket() {
      $('.appBasket').css({visibility: 'visible'});
    },

    hideBasket() {
      $('.appBasket').css({visibility: 'hidden'});
    },

    getInitialState() {
      return {
        loggedIn: auth.loggedIn()
      }
    },

    updateAuth(loggedIn) {
      this.setState({
        loggedIn: !!loggedIn
      })
    },

    componentWillMount() {
      auth.onChange = this.updateAuth
      auth.login()
    },

    render() {
      var that = this;
      var shoppingMallItems = [{name: '', link: '/products-by-category/self_sale'},
        {name: '', link: '/products-by-category/kaixian_special'},
        {name: '', link: '/products-by-category/xiangcuntuhuo'},
        {name: '', link: '/products-by-category/gongyechanping'},
        {name: '', link: '/products-by-category/xianwaichanping'},
        {name: '', link: '/products-by-category/imported'}
      ];
      var travelItems = [{name: '', link: '#'},
        {name: '', link: '#'},
        {name: '', link: 'http://bbs.cqkx.com/forum-142-1.html'},
        {name: '', link: '#'},
        {name: '', link: '#'},
        {name: '', link: 'http://bbs.cqkx.com/forum-129-1.html'}
      ];
      var convenienceLifeItems = [{
        name: '',
        link: 'https://jiaofei.alipay.com/jiaofei.htm?_pdType=aecfbbfgeabbifdfdieh&_scType=bacfajegafddadijhagd'
      },
        {name: '', link: 'http://bbs.cqkx.com/forum.php?mod=forumdisplay&fid=288'},
        {name: '', link: 'http://www.kzrc.gov.cn/'},
        {name: '', link: 'http://bbs.cqkx.com/forum-129-1.html'},
        {name: '', link: 'http://bbs.cqkx.com/forum-131-1.html'},
        {
          name: '',
          link: 'http://job.cqkx.com/index.php?m=com&c=search&hy=&jobids=&cityid=&pr=&mun=&exp=&salary=&edu=&up'
        }
      ];
      var financeServiceItems = [{name: '', link: 'http://www.kqjr.cn'},
        {name: '', link: 'http://www.cqkxxn.com/'},
        {name: '', link: 'http://www.kqjr.cn'},
        {name: '', link: '/finance-service/articles/bank_services'},
        {name: '', link: 'https://yebprod.alipay.com/yeb/index.htm'},
        {name: '', link: 'http://www.kqjr.cn/Article/Index?code=news'}
      ];
      var innovationSpaceItems = [{name: '', link: '/innovation-space/articles/customer_show'},
        {name: '', link: '/innovation-space/articles/incubation'},
        {name: '', link: '/innovation-space/articles/cooperation'},
        {name: '', link: '/innovation-space/activities'},
        {name: '', link: '/innovation-space/articles/education'},
        {name: '', link: '/innovation-space/articles/guest_house'}
      ];
      var companyItems = [{name: '', link: '/customers'},
        {name: '', link: '/company-service/articles/company_policies'},
        {name: '', link: '/company-service/articles/experts'},
        {name: '', link: '/company-service/articles/business_running'},
        {name: '', link: '/company-service/articles/law_services'},
        {name: '', link: '/company-service/articles/association_service'}
      ];

      var className = 'pure-g' + (this.props.isMobile ? ' mobile-header' : '');

      // <div className='basket-container' onMouseOver={that.displayBasket} onMouseOut={that.hideBasket}>
      //   <Basket />
      // </div>

      return (
        <div className={className}>
          <header className="appHeader pure-u-1">
            <div className="top-nav">
              <ul className="right clear">
                  {this.state.loggedIn ? (
                    <li>
                      <span className='user-email'>{localStorage.email}</span>
                      <a href="/logout"></a>
                    </li>
                  ) : (
                    <li>
                      <a href="/login"></a>
                    </li>
                  )}
                {this.state.loggedIn ? ('') : (<li>
                  <a href="/register"></a>
                </li>)}
                <li>
                  <a href="/consumer"></a>
                </li>
                <ShoppingCart />
              </ul>
            </div>
            <div className="middle-nav">
              <a href='/'><image className='logo-image' src='/assets/images/products/logo.jpg' /></a>
              <image className='logo-title' src='/assets/images/products/logo-title.jpg' />
              <SearchBar />
            </div>
            <div className="bottom-nav">
              <ProductCategory display={this.props.display}/>
              <MenuItem menuIndex={1} subMenu={shoppingMallItems} name='' link='/' />
              <MenuItem menuIndex={2} subMenu={travelItems} name='' link='/travel.html' />
              <MenuItem menuIndex={3} subMenu={convenienceLifeItems} name='' link='/convenience-life/articles/jiaofei' />
              {/*<MenuItem menuIndex={4} subMenu={financeServiceItems} name='' link='/finance-service/articles/investment' />*/}
              <MenuItem menuIndex={5} subMenu={innovationSpaceItems} name='' link='/innovation-space/activities' />
              <MenuItem menuIndex={6} subMenu={companyItems} name='' link='/company-service/articles/company_settlement' />
            </div>
          </header>
        </div>
      );
    }
  });
export default Header



